
import { useEffect, useState } from 'react';
import './App.css';
import $ from 'jquery'

//assets
import iredoc from './assets/img/iredoc.png'

//components
import QRCoode from './QrCode'

function App() {

  let [name, setName] = useState("")
  let [studentNum, setStudent] = useState("")
  let [guild, setGuild] = useState("")
  let [section, setSection] = useState("")
  let [data, setData] = useState("");

  let [isQRShown, setIsQRShown] = useState(false);

  const submitData = () => {
    setData(studentNum + "[|]" + name + "[|]" + guild + "[|]" + section);
    setIsQRShown(current => !current);
  }

  return (
    <div className="App">
      <div>
        <hgroup>
          <h1>LISTA Attendance shit</h1>
          <h3>By Andrew Tate (Top G)</h3>
        </hgroup>
        <form>
          <div className="group">
            <input onChange={e => setName(e.target.value)} id='name' type="text" value={name} /><span className="highlight" /><span className="bar" />
            <label>Full Name (Surname, First Name M.I)</label>
          </div>
          <div className="group">
            <input onChange={e => setStudent(e.target.value)} id='studentNum' type="text" /><span className="highlight" /><span className="bar" />
            <label>Student Number</label>
          </div>
          <div className="group">
            <input onChange={e => setGuild(e.target.value)} id='guild' type="text" value={guild} /><span className="highlight" /><span className="bar" />
            <label>Guild</label>
          </div>
          <div onChange={e => setSection(e.target.value)} className="group">
            <input id="section" type="text" /><span className="highlight" value={section} /><span className="bar" />
            <label>Section</label>
          </div>
          <button onClick={submitData} type="button" className=" button buttonBlue">Submit
            <div className="ripples buttonRipples"><span className="ripplesCircle" /></div>
          </button>
        </form>
        <footer><a href="http://www.facebook.com/cdrcspn" without rel="noreferrer" target="_blank"><img alt="IREDOC#1" src={iredoc} /></a>
          <p>STI LISTA Club. All Rights Reserved.</p>
        </footer>
      </div>
      {isQRShown && <QRCoode data={data} />}
    </div >
  );
}
























$(window, document, undefined).ready(function () {

  $('input').blur(function () {
    var $this = $(this);
    if ($this.val())
      $this.addClass('used');
    else
      $this.removeClass('used');
  });

  var $ripples = $('.ripples');

  $ripples.on('click.Ripples', function (e) {

    var $this = $(this);
    var $offset = $this.parent().offset();
    var $circle = $this.find('.ripplesCircle');

    var x = e.pageX - $offset.left;
    var y = e.pageY - $offset.top;

    $circle.css({
      top: y + 'px',
      left: x + 'px'
    });

    $this.addClass('is-active');

  });

  $ripples.on('animationend webkitAnimationEnd mozAnimationEnd oanimationend MSAnimationEnd', function (e) {
    $(this).removeClass('is-active');
  });

});

export default App;
